#dot1, #dot2, #dot3, #dot4, #dot5, #dot6, #dot7, #dot8, #dot9, #dot10 {
  width: 10px;
  height: 10px;
  transform-origin: center;
  background: #000;
  border-radius: 50px;
  animation: spin 1.5s linear infinite;
  position: absolute;
}

#dot2 {
  animation: spin 1.5s linear -.75s infinite;
}

#dot3 {
  animation: spin 1.5s linear -1.3s infinite;
  left: 30px;
}

#dot4 {
  animation: spin 1.5s linear -.55s infinite;
  left: 30px;
}

#dot5 {
  animation: spin 1.5s linear -1.1s infinite;
  left: 60px;
}

#dot6 {
  animation: spin 1.5s linear -.35s infinite;
  left: 60px;
}

#dot7 {
  animation: spin 1.5s linear -.9s infinite;
  left: 90px;
}

#dot8 {
  animation: spin 1.5s linear -.15s infinite;
  left: 90px;
}

#dot9 {
  animation: spin 1.5s linear -.7s infinite;
  left: 120px;
}

#dot10 {
  animation: spin 1.5s linear 50ms infinite;
  left: 120px;
}

#base1, #base2, #base3, #base4, #base5 {
  width: 1px;
  height: 10px;
  transform-origin: center;
  background: #000;
  animation: flex 1.5s linear infinite;
  position: absolute;
  top: 30px;
  left: 5px;
}

#base2 {
  animation: flex 1.5s linear -1.3s infinite;
  left: 35px;
}

#base3 {
  animation: flex 1.5s linear -1.1s infinite;
  left: 65px;
}

#base4 {
  animation: flex 1.5s linear -.9s infinite;
  left: 95px;
}

#base5 {
  animation: flex 1.5s linear -.7s infinite;
  left: 125px;
}

@keyframes spin {
  0%, 100% {
    transform: translateY(0)scale(1);
  }

  25% {
    transform: translateY(30px)scale(2);
  }

  50% {
    transform: translateY(60px)scale(1);
  }

  75% {
    transform: translateY(30px)scale(.3);
  }
}

@keyframes flex {
  0%, 100% {
    transform: scaleY(5);
  }

  25% {
    transform: scaleY(1);
  }

  50% {
    transform: scaleY(5);
  }

  75% {
    transform: scaleY(1);
  }
}

