.movie-card {
  width: 280px;
  cursor: pointer;

  border-radius: 5px;

  display: flex;
  flex-direction: column;

  @media screen and (min-width: $tablet) {
    width: calc((100% - 32px) / 2);
  }

  @media screen and (min-width: $desktop) {
    width: calc((100% - 32px) / 3);
  }

  &:hover,
  &:focus {
    box-shadow: 1px 8px 20px grey;
    transition: box-shadow 0.3s ease-in;
  }

  &__img {
    height: 398px;
    border-radius: 5px;
    // max-width: 100%;

    @media screen and (min-width: $tablet) {
      height: 455px;
    }

    @media screen and (min-width: $desktop) {
      height: 574px;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding-right: 10px;
    padding-left: 10px;

    padding-top: 8px;
    padding-bottom: 8px;

    @media screen and (min-width: $desktop) {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  &__name {
    text-transform: uppercase;
    line-height: 1.3;

    @media screen and (min-width: $desktop) {
      font-size: 20px;
      line-height: 1.2;
    }
  }

  &__info-wrap {
    color: $accentColor;

    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  // &__rating {
  //   width: 36px;
  //   height: 16px;
  //   background: $accentColor;
  //   border-radius: 5px;

  //   font-size: 12px;

  //   display: flex;
  //   align-items: center;
  //   justify-content: center;
  //   text-transform: uppercase;

  //   color: $secondaryColor;

  //   @media screen and (min-width: $desktop) {
  //     width: 40px;
  //     height: 20px;
  //   }
  // }
}
