.backdrop {
  position: fixed;
  top: 0;
  padding: 20px 20px;
  display: flex;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Roboto, sans-serif;

  background-color: $backdropColor;
  overflow: scroll;

  @media screen and (min-width: $tablet) {
    padding: 32px 0;
  }

  @media screen and (min-width: $desktop) {
    column-gap: 16px;
  }
}

.modal__close {
  position: absolute;
  top: 8px;
  right: 8px;
  border: none;
  background-color: transparent;
  outline: none;
}
